body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* NAV START */

#logo {
	font-family: Georgia, Times, "Times New Roman", serif;
	/* Typography */
	font-size: 24px;
	font-weight: bold;
	color: #fff;
	/* Visual */
	background: #4aaaa5;
	padding-left: 5px;
	padding-right: 5px;
}

.navbar {
	background: white;
	border: none;
}

/* media query for nav background color change */

@media only screen and (max-width: 767px) {
	.navbar-header {
		background-color: #4aaaa5;
	}
}
/* END NAV */
.card {
	background: #fff;
	border-radius: 2px;
	height: 325px;
	margin: 1rem;
	position: relative;
	width: 325px;
	box-shadow: 0 3px 6px #999, 0 3px 6px #999;
	text-align: left;
}

.card>.img-container {
	height: 60%;
	overflow: hidden;
	text-align: center;
	background: #6CADDC;
}

.card>.img-container>img {
	width: 60%
}

.card>.content {
	padding-left: 1rem;
	padding-right: 1rem;
	font-size: 15px;
}

.card>.content>ul {
	list-style-type: none;
}

.card>.content>ul>li {
	margin-top: 18px;
}

.card>.remove {
	color: #ff1744;
	font-size: 48px;
	position: absolute;
	top: -20px;
	right: 5px;
	cursor: pointer;
}

.card>.remove:hover {
	color: #d50000;
}
.wrapper {
	padding-top: 50px;
	background: #ECEFF1;
	height: 100%;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-flow: row wrap;
	        flex-flow: row wrap;
	padding: 20px;
	-webkit-justify-content: space-around;
	        justify-content: space-around;
	-webkit-align-content: flex-start;
	        align-content: flex-start;
	overflow: auto;
}
.title {
  width: 100%;
  text-align: center;
  font-size: 3rem;
  /* text-decoration: underline; */
  color: #2A77AE;
  margin-bottom: 25px;
}

.alert {
	margin: 25px;
}
.alert {
	margin: 25px;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

